import React, {useEffect, useState} from 'react';
import API from "./salary_data.json";
import "./App.css";


function App() {
  const [data, setData] = useState([]);
  const [salary, setSalary] = useState([]);
  const [convert, setConvert] = useState(0);

  useEffect(() => {
    const url ="http://jsonplaceholder.typicode.com/users";
    const urlConvert ="https://free.currconv.com/api/v7/convert?q=USD_IDR&compact=ultra&apiKey=1a857a3db5eefb93f12d";

    const fetchData = async () =>{
      try{
        const response = await fetch(url, {
          method: "GET"
        });
        const json = await response.json();
        setData(json)
        setSalary(API.array)
      }catch(error){
        console.log(error, "error");
      }
    }
    const fetchUSD = async () =>{
      try{
        const response = await fetch(urlConvert, {
          method: "GET"
        });
        const json = await response.json();
        setConvert(json.USD_IDR)
      }catch(error){
        console.log(error, "error");
      }
    }
    fetchData();
    fetchUSD();

  },[])

  return (
    <div className='App'>
      <h1> Salary Conversion </h1>
      <tr className='thead'>
        <th>ID</th>
        <th>Name</th>
        <th>Username</th>
        <th>Email</th>
        <th>Address</th>
        <th>Phone</th>
        <th>Salary in IDR</th>
        <th>Salary in USD</th>
      </tr>
      {data.map(item =>{
        return <tr>
            <td className='itemid'>{item.id}</td>
            <td className='item'>{item.name}</td>
            <td className='item'>{item.username}</td>
            <td className='item'>{item.email}</td>
            <td className='item'>{`${item.address.street} ${item.address.suite} ${item.address.city}`}</td>
            <td className='item'>{item.phone}</td>
            {salary.map(x => {
                if(item.id === x.id){
                  return <>
                  <td className='item'>{`Rp ${Math.floor(x.salaryInIDR)}`}</td>
                  <td className='item'>{`$ ${Math.floor(x.salaryInIDR/convert)}`}</td>
                  </>
                }
              })
            }
      </tr>
      })}
    </div>
  );
}

export default App;
